import { Avatar } from '@material-ui/core'
import { AccountCircle, ChatBubbleOutline , ExpandMoreOutlined, NearMe } from '@material-ui/icons'
import ThumbUp from "@material-ui/icons/ThumbUp"
import React from 'react'
import styled from 'styled-components'

function Post({ profilePic , image , username , timestamp , message }) {
    return (
        <Container>
            <Top>
                <Avatar src={profilePic} />
                <PostInfo>
                    <h3>{username}</h3>
                    <p>{timestamp && new Date(timestamp.toDate()).toUTCString() }</p>
                </PostInfo>
            </Top>

            <Bottom>
                <p>{message}</p>
            </Bottom>

            <PostImage>
                <img src={image} alt="" />
            </PostImage>

            <Options>
                <Option>
                    <ThumbUp/>
                    <p>Like</p>
                </Option>
                <Option>
                    <ChatBubbleOutline/>
                    <p>Comment</p>
                </Option>
                <Option>
                    <NearMe/>
                    <p>Share</p>
                </Option>
                <Option>
                    <AccountCircle/>
                    <ExpandMoreOutlined />
                </Option>
            </Options>
        </Container>
    )
}

export default Post

const Options = styled.div`
    padding-top : 10px ;
    border-top : 1px solid lightgray ;
    display : flex ;
    justify-content : space-evenly ;
    font-size : medium ;
    color : gray ;
    cursor : pointer ;
    padding : 15px ;
`

const Option = styled.div`
    display : flex; 
    align-items : center ;
    justify-content : center ;
    padding : 5px ;
    flex : 1 ;

    :hover {
        background-color : #eff2f5 ;
        border-radius : 10px ;
    }
`

const Bottom = styled.div`
    margin-top : 10px ;
    margin-bottom : 10px ;
    padding : 15px 25px ;
`

const PostImage = styled.div`
    img {
        max-height : calc(100vh - 150px) ;
        width : 100% ;
        object-fit : contain ;
    }
`

const Top = styled.div`
    display : flex ;
    position : relative ;
    align-items : center ;
    padding : 15px ;

    .MuiAvatar-root {
        margin-right : 10px ;
    }
`

const PostInfo = styled.div`
    h3 {
        font-size : medium ;
    }

    p {
        font-size : small ;
        color : gray ;
    }
`

const Container = styled.div`
    width: 100% ;
    margin-top : 15px ;
    border-radius : 15px ;
    background-color : white ;
    box-shadow : 0px 5px 7px -7px rgba(0 , 0 , 0 , 0.75) ;
`
